<template>

    <div :style="style">
        <div style="margin-bottom: 25px">
            <i class="el-icon-plus"></i><span>添加</span>
            <div style="margin-top:10px;">
                <el-button type="primary" @click="addCourse()" plain>添加</el-button>
            </div>
        </div>
        <div>
            <div>
                <i class="el-icon-search"></i><span>查询</span>
                <div class='searchContent' style="margin-top:10px;">
                    <el-form :model="searchData" inline>
                        <el-form-item label="课程名:">
                            <el-input v-model.trim="searchData.courseName"></el-input>
                        </el-form-item>
                        <el-form-item label="专业号:">
                            <el-input v-model.trim="searchData.majorId"></el-input>
                        </el-form-item>
                        <el-form-item label="年级:">
                            <el-input v-model.trim="searchData.grade"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" plain>查询</el-button>
                            <el-button type="success" @click="examineAll" plain>重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div>
                <div>
                    <i class="el-icon-document"><span>课程列表</span></i>
                </div>
            </div>

        </div>
        <el-table
                :data="courseList"
                style="width: 100%"
                height="100%">
            <el-table-column
                    fixed
                    prop="courseId"
                    label="课程号"
            >
            </el-table-column>
            <el-table-column
                    fixed
                    prop="courseName"
                    label="课程名"
            >
            </el-table-column>
            <el-table-column
                    fixed
                    prop="majorId"
                    label="专业号"
            >
            </el-table-column>
            <el-table-column
                    prop="grade"
                    label="年级"
            >
            </el-table-column>

        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                @current-change="handlePageChange"
                :total="pageInfo.total"
                :page-size="pageInfo.pageSize"
                :current-page="pageInfo.pageNum">
        </el-pagination>
        <transition name="fade" mode="out-in">
            <el-dialog title="添加课程" :visible.sync="dialogFormVisible"
                       v-loading="loading"
                       element-loading-text="拼命加载中"
                       element-loading-spinner="el-icon-loading"
                       element-loading-background="rgba(0, 0, 0, 0.8)">
                    <el-form :model="course" >
                        <el-form-item label="课程号" :label-width="formLabelWidth">
                            <el-input v-model="course.courseId" autocomplete="on" size="normal"></el-input>
                        </el-form-item>
                        <el-form-item label="课程名" :label-width="formLabelWidth">
                            <el-input v-model="course.courseName" autocomplete="on" size="normal"></el-input>
                        </el-form-item>
                        <el-form-item label="年级" :label-width="formLabelWidth">
                            <el-input v-model="course.grade" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="专业号" :label-width="formLabelWidth">
                            <el-input v-model="course.majorId" autocomplete="on"></el-input>
                        </el-form-item>
                    </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="commitMessage()">确 定</el-button>
                </div>
            </el-dialog>
        </transition>
    </div>

</template>

<script>
export default {
  name: 'page',
  data () {
    return {
      loading: false,
      cascaderValue: ['hehe', 'hah'], // 级联框的选择结果
      cascaderOptions: [],
      style: {
        height: window.innerHeight + 'px'
      },
      dialogFormVisible: false,
      formLabelWidth: '120px',
      courseList: [
        {
          courseName: '计算机',
          grade: '2013', // 年级
          majorId: '02' // 专业ID

        },
        {
          courseName: '音乐',
          grade: '2013', // 年级
          majorId: '02' // 专业ID

        }
      ],

      course: {
        courseId: '',
        courseName: '',
        grade: '', // 年级
        majorId: '' // 专业ID
      },
      searchData: { // 搜索的数据
        courseId: '',
        courseName: '',
        grade: '',
        majorId: ''
      },
      pageInfo: {
        total: 100,
        pageSize: 15,
        pageNum: 1
      }
    }
  },
  methods: {
    /**
             * 修改页数
             */
    handlePageChange (val) {
      this.pageInfo.pageNum = val
      this.getCourseList()
    },
    /**
             * 添加学生
             */
    addCourse () {
      this.dialogFormVisible = true
      this.course = {}
    },
    /**
             * 提交参数
             */
    commitMessage () {
      this.loading = true
      let data = this.course

      let callback = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('成功', 'success', '添加成功')
      }
      let error = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('警告', 'warning', '添加失败')
      }
      let exception = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('错误', 'error', '请求错误')
      }
      this._ajax('post', '/course/management/add', callback, {}, error, exception, data)
    },
    /**
             * 搜索学生
             */
    search () {
      if (this.searchData.courseId === '' && this.searchData.courseName === '' &&
                    this.searchData.grade === '' && this.searchData.majorId === '') {
        this.getCourseList()
      } else {
        let params = this.searchData
        let callback = (res) => {
          console.log(res)
          this.courseList = res.charData
        }
        let error = (res) => {
          console.log(res)
        }
        this._ajax('post', '/course/management/search', callback, {}, error, () => {}, params)
      }
    },
    /**
             * 重置查询内容
             */
    examineAll () {
      this.searchData = {}
    },
    getCourseList () {
      let callback = (res) => {
        this.courseList = res.charData.list
        this.pageInfo.pageSize = res.charData.pageSize
        this.pageInfo.total = res.charData.total
      }
      let error = (res) => {
        console.log(res)
      }
      let param = this.pageInfo
      this._ajax('get', '/course/management/list', callback, param, error, () => {})
    }
  },
  mounted () {
    console.log(this.platform)
    this.getCourseList()
  }
}
</script>

<style scoped>
    .btnFun {
        text-align: center;
        margin: 50px;
    }
    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    .bounce-leave-active {
        animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }
</style>



